.loadingCircle {
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-fill-mode: both;
  animation-name: pulse;
  /* transform-origin: center center; */
}

@keyframes pulse {
  from {
    transform: scale3d(0.8, 0.8, 0.8);
  }

  50% {
    transform: scale3d(1.05, 1.05, 1.05);
  }

  to {
    transform: scale3d(0.8, 0.8, 0.8);
  }
}

.menuPanel {
  position: absolute;
  top: 75px;
  left: 0;
  height: 120;
  width: 100%;
  z-index: 100;
  display: flex;
  justify-content: center;
  align-items: center;
  pointer-events: none;
}

@media (min-width: 440px) {
  .menuPanel {
    top: 90px;
  }
}

@media (min-width: 600px) {
  .menuPanel {
    top: 27px;
  }
}

@media (min-width: 730px) {
  .menuPanel {
    z-index: 1000;
    top: 27px;
    right: 30px;
    justify-content: flex-end;
    align-items: center;
    padding-right: 27px;
  }
}

@media (min-width: 900px) {
  .menuPanel {
    z-index: 100;
    top: 210px;
    right: 30px;
    justify-content: flex-start;
    align-items: center;
  }

  .tooltip {
    top: 60% !important;
  }
}

/* SELECT */

.select-css {
  pointer-events: auto;
  display: inline-block;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.2em;
  width: 100%;
  max-width: 100%;
  box-sizing: border-box;
  /* margin: 0.5em; */
  padding: 0.5em 0.5em 0.5em 1em;
  border: 1px solid var(--foreground-color);
  border-radius: 0;
  -moz-appearance: none;
  appearance: none;
  color: var(--foreground-color);
  background-color: var(--background-color);
  background-image: url("data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22gray%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E"),
    linear-gradient(
      to bottom,
      var(--background-color) 0%,
      var(--background-color) 100%
    );
  background-repeat: no-repeat, repeat;
  background-position: right 1em top 50%, 0 0;
  background-size: 0.65em auto, 100%;
}
.select-css::-ms-expand {
  display: none;
  color: var(--foreground-color);
}
.select-css:focus {
  color: var(--foreground-color);
  outline: none;
  color: var(--foreground-color);
}
.select-css option {
  color: var(--foreground-color);
  font-weight: normal;
}

select:disabled {
  opacity: 0.2;
}
